/* General Styles */

/* Adjusted title color to a lighter shade */
.title {
  text-align: center;
  font-size: 2.5rem;
  color: red;  /* Lighter gray color */
  margin-bottom: 30px;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.database-demo {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.title {
  text-align: center;
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 30px;
}

.form-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}

.form-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.form-row input {
  width: 30%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

.form-row input:focus {
  border-color: #007BFF;
  outline: none;
}

.btn {
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-btn {
  background-color: #28a745;
}

.add-btn:hover {
  background-color: #218838;
}

.del-btn {
  background-color: #dc3545;
}

.del-btn:hover {
  background-color: #c82333;
}

.movies-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.movies-table th, .movies-table td {
  padding: 12px;
  text-align: left;
  border: 1px solid #ddd;
}

.movies-table th {
  background-color: #f1f1f1;
  font-weight: bold;
}

.movies-table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

.movies-table img {
  max-width: 50px;
  border-radius: 4px;
}

@media (max-width: 768px) {
  .form-row {
    flex-direction: column;
  }

  .form-row input {
    width: 100%;
    margin-bottom: 10px;
  }

  .form-row button {
    width: 100%;
    margin-bottom: 10px;
  }

  .movies-table th, .movies-table td {
    font-size: 0.9rem;
    padding: 8px;
  }
}
